	import img12 from '../assets/img12.jpg'
	import img13 from '../assets/img13.jpg'
	import img14 from '../assets/img14.jpg'


	var moviesInfo=[
			
			{
				rank:"1",
				title:"飞狐外传",
				director:"弗兰克·德拉邦特 Frank Darabont",
				imgSrc:img1
			},
			{
				rank:"2",
